<template>
        <form class="form-row justify-content-center text-center">
            <div class="col-6">
                <label> Add a new Quote: </label>
                <textarea class="form-control" rows="3" placeholder="Add a new Quote here..." v-model="localText"></textarea>           
            
                <button class="btn btn-outline-primary" @click.prevent="click"> Add Quote </button>
            </div>
        </form>
</template>

<script>
    export default {
        data(){
            return{
                localText: ""
            }
        },
        methods:{
            click(){
                if(this.localText != ""){
                    this.$emit('newQuoteAdded', this.localText);
                    this.localText = "";
                }
            }
        }
    }
</script>

<style scoped>
    textarea {
        resize: none;
    }

    .btn {
        margin: 10px;
    }
</style>