<template>
    <div class="col-sm-6 col-md-4 col-lg-3">
        <div class="card quote" @click="click">
            <div class="card-body">
                <slot>
                    Default Text
                </slot>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        props:['index'],
        methods:{
            click(){
                this.$emit("quoteClicked", this.index);
            }
        }
    }
</script>

<style scoped>
    .card-body{
        font-family: 'Great Vibes', cursive;
        font-size: 24px;
        color: #6e6e6e;
    }

    .quote{
        cursor: pointer;
        text-align: center;
        margin: 10px;
    }

    .quote:hover{
        background-color: #ffe2e2;
    }
</style>