<template>
    <div class="row ">
        <div class="col-sm-12 text-center">
            <h1> Quotes Added </h1>
            <div class="progress">
                <div class="progress-bar" :style="'width:'+progress+'%'"> <template v-if="progress > 0">{{progress}}%</template></div>
            </div>
            <h4><small> You have used {{quoteCount}}/{{maxQuotes}} quotes.</small></h4>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['quoteCount', "maxQuotes"],
        computed:{
            progress: function(){
                return Math.round(this.quoteCount / this.maxQuotes * 100);
            }
        }
    }
</script>

