<template>
    <div class="row justify-content-center" >
        <appQuote 
        v-for="(quote, i) in quotes" 
        :key="quote" :index = "i"
        @quoteClicked= "deleteQuote($event)">
            {{quote}}
        </appQuote>
    </div>
</template>

<script>
    import Quote from './Quote';
    export default {
        props: ['quotes'],
        components:{
            'appQuote': Quote
        },
        methods:{
            deleteQuote(index){
                this.$emit('quoteDeleted', index);
            }
        }
    }
</script>

